.card {
    display: inline-block;
    width: 300px;
    
    padding: 20px;
    border: 1px solid black;
    
    filter: grayscale(100%);
    font-family: 'Roboto', sans-serif;
}
.card:hover {
    filter: grayscale(0%);
    cursor: pointer; 
}
.card > img {
    
    width: 100%;
    margin-bottom: 10px;
}
.card > p {
    font-size: 14px;
}
.cardTitle {
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 20px;
}