button {
    padding: 20px;
    width: auto;
    height: auto;
    background: rgb(255,0,0);
    background: linear-gradient(357deg, rgba(255,0,0,1) 0%, rgba(255,143,0,1) 100%);
    color: white;
    font-size: 20px;
    font-weight: bold;
    border: none;
    border-radius: 30px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
button:hover {
    cursor: pointer;
}